.TodoItem {
    background-color: rgb(221, 217, 217);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.15);
    border-radius: 8px;
}
.TodoItem-p{
    
    margin: 24px 0 24px 24px;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
}
.TodoItem-p--complete {
    text-decoration: line-through;
}

.Icon {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 40px;
    width: 40px;
    font-size: 24px;
    font-weight: bold;

}
.Icon-check {
    position: absolute;
    left: 12px;
}
.Icon-check--active{
    color: green;
}
.Icon-delete{
    position: absolute;
    top: -24px;
    right: 0;
}


.Icon-delete:hover {
    color: red;

}